<script>
    import MyOverlay from '../components/MyOverlay.vue'
    export default {
        name: "Map_base",
        data () {
            return {
                show:false,
                active: false,
                message: [],
                message2: []
            }
        },
        components: {
            MyOverlay
        },
        created () {
            this.getData()
        },
        methods: {
            infoWindowClose () {
                this.show = false
            },
            infoWindowOpen () {
                this.show = true
            },
            getData() {
                var string1 = "https://www.tianqiapi.com/api/?version=v1&cityid=";
                var string2 = "&appid=[77843946]&appsecret=[4n52Km4v]";
                var cityId = [10];
                cityId[0] = 101280101;
                cityId[1] = 101281201;
                cityId[2] = 101280102;
                cityId[3] = 101280201;
                cityId[4] = 101280401;
                cityId[5] = 101281401;
                cityId[6] = 101280601;
                cityId[7] = 101281801;
                cityId[8] = 101280501;
                cityId[9] = 101282101;
                this.axios.get(string1 + cityId[0] + string2).then((res) => {
                    document.getElementById(10).innerText =
                        "城市：" + res.data.city +
                        "\n风级：" + res.data.data["0"].win_speed +
                        "\n空气指数：" + res.data.data["0"].air_level + "\n" + res.data.data["0"].air_tips +
                            "\n\n紫外线指数：" + res.data.data["0"].index["0"].level + "\n" + res.data.data["0"].index["0"].desc +
                        "\n\n穿衣指数：" + res.data.data["0"].index["3"].level + "\n" + res.data.data["0"].index["3"].desc
                });
                for (let i = 0; i < 10; i++) {
                    this.axios.get(string1 + cityId[i] + string2).then((res) => {
                        this.message[i] = res.data.data["0"].date +"\n平均温度：" + res.data.data["0"].tem + "\n天气：" + res.data.data["0"].wea;
                        document.getElementById(i).innerText = this.message[i];
                        // eslint-disable-next-line no-console
                        console.log(res);
                    })
                }
            }
        }
    }
</script>

