<template>
    <bm-overlay
            :class="{sample: true}"
            pane="labelPane"
            @draw="draw">
    </bm-overlay>
</template>

<script>
    export default {
        name: "MyOverlay",
        props: ['position'],
        watch: {
            position: {
                handler () {
                    this.$refs.customOverlay.reload()
                },
                deep: true
            },
        },
        methods: {
            draw ({el, BMap, map}) {
                const {lng, lat} = this.position;
                const pixel = map.pointToOverlayPixel(new BMap.Point(lng, lat));
                el.style.left = pixel.x - 50 + 'px';
                el.style.top = pixel.y + 5 + 'px';
            },
            show({el}){
                el.style.display = ""
            },
            hide({el}){
                el.style.display = "none"
            }
        }
    }
</script>

<style scoped>
    .sample {
        width: 100px;
        height: 45px;
        line-height: 13px;
        background: rgba(0,0,0,0.5);
        overflow: hidden;
        box-shadow: 0 0 5px #000;
        color: #fff;
        font-size: 13px;
        text-align: center;
        padding: 2px;
        position: absolute;
    }
    .sample.active {
        background: rgba(0,0,0,0.75);
        color: #fff;
    }
</style>